@-ms-viewport {
  width: device-width;
}

@-o-viewport {
  width: device-width;
}

@viewport {
  width: device-width;
}

html {
  box-sizing: border-box;
}

*,
* ::before,
* ::after {
  box-sizing: inherit;
}

html {
  -webkit-font-smoothing: antialiased;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
  -moz-osx-font-smoothing: grayscale;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100%;
  min-height: 100vh;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  line-height: 1.5;
  background-color: #f7f7f7;
}

a {
  text-decoration: none;
  color: #0572CE;
}

a:not([class]):hover {
  text-decoration: underline;
}

p {
  margin: 0 0 24px;
}

p:last-child {
  margin-bottom: 0;
}

h1 {
  margin: 0 0 16px;
  font-size: 48px;
}

h2 {
  margin: 0 0 12px;
  font-size: 32px;
}

h3 {
  margin: 0 0 8px;
  font-size: 24px;
}

h4 {
  margin-bottom: 4px;
  font-size: 20px;
}


code,
pre {
  font-size: 90%;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  background-color: rgba(0,0,0,.025);
  box-shadow: inset rgba(0,0,0,.05) 0 0 0 1px;
}

code {
  padding: 2px 4px;
  border-radius: 2px;
}

pre {
  padding: 8px;
  border-radius: 4px;
  margin-bottom: 16px;
}

.icon-class {
  color: #17756c;
}
.modifier-class {
  color: #9c27b0
}


.dm-AccessibleHidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}



.dm-Header {
