  background-position: 50%;
  background-size: 16px;
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 8px;
  align-items: center;
  justify-content: center;
}

.dm-Toggle {
  position: relative;
  display: block;
  margin: 0;
  width: 100px;
  height: 48px;
  background-color: #fff;
  outline: none;
  border-radius: 4px;
  box-shadow: inset rgba(0,0,0,.15) 0 0 0 1px;
  cursor: pointer;
  transition: .1s ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.dm-Toggle:after {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 48px;
  height: 40px;
  content: "Small";
  text-align: center;
  font-weight: 700;
  font-size: 11px;
  line-height: 40px;
  color: rgba(0,0,0,.65);
  background: transparent;
  background-color: #f0f0f0;
  border-radius: 2px;
  box-shadow: inset rgba(0,0,0,.15) 0 0 0 1px;
  transition: .1s ease;
}

.dm-Toggle:checked {
  background-color: #0572CE;
}

.dm-Toggle:checked:after {
  left: 48px;
  content: 'Large';
  color: #0572CE;
  background-color: #fff;
  box-shadow: rgba(0,0,0,.15) 0 0 0 1px;
}



.dm-RadioPillSet {
  display: flex;
  width: 100%;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: inset 0 0 0 1px rgba(0,0,0,.15);
}

.dm-RadioPillSet-option {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
}

.dm-RadioPillSet-option:not(:first-child) {
  border-left: 1px solid rgba(0,0,0,.15);
}

.dm-RadioPillSet-option:first-child input + label {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.dm-RadioPillSet-option:last-child input + label {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.dm-RadioPillSet-option input {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
}

.dm-RadioPillSet-option input + label {
  display: block;
  padding: 8px 12px;
  text-align: center;
  font-size: 12px;
  line-height: 16px;
  color: #383838;
  cursor: pointer;
}

.dm-RadioPillSet-option input:checked + label {
  font-weight: 700;
  color: #f9f9f9;
  background-color: #0572CE;
}

.dm-RadioPillSet-option input:focus + label {
  box-shadow: inset #0572CE 0 0 0 1px, inset #fff 0 0 0 2px;
}

.dm-RadioPillSet--large .dm-RadioPillSet-option input + label {
  padding: 5px 24px;
  font-size: 14px;
  line-height: 20px;
}







